import React from 'react'
import './css/header.css'
import { NavData } from './NavData'
export const Header = () => {
    return (
        <header>
            <div className="container">
                <div className="col-1">
                    <h1 className="brand-name">Flone.</h1>
                </div>
                <div className="col-2">
                    <div className="navbar">
                        <div className="close">
                            <i className="fad fa-times"></i>
                        </div>
                        <ul>
                            {/* <li><a>Home <i className="far fa-angle-down"></i></a></li>
                            <li><a>Shop <i className="far fa-angle-down"></i></a></li>
                            <li><a>Collection</a></li>
                            <li><a>Pages <i className="far fa-angle-down"></i></a></li>
                            <li><a>Blog <i className="far fa-angle-down"></i></a></li>
                            <li><a>Contact Us</a></li> */}

                            {NavData.map((val, key) => {
                                return(
                                    <li><a href={val.path}>{val.title} <i className={val.icon}></i></a></li>
                                )
                            })}            

                        </ul>

                        <ul className="nav-icons">
                            <li><i className="fal fa-search"></i></li>
                            <li><i className="fal fa-user"></i></li>
                            <li><i className="fal fa-retweet"></i></li>
                            <li><i className="fal fa-heart"></i></li>
                            <li><i className="fal fa-shopping-bag"></i></li>
                        </ul>

                    </div>
                </div>
                <div className="col-3">
                    <ul className="nav-icons">
                        <li><i className="fal fa-search"></i></li>
                        <li><i className="fal fa-user"></i></li>
                        <li><i className="fal fa-retweet"></i></li>
                        <li><i className="fal fa-heart"></i></li>
                        <li><i className="fal fa-shopping-bag"></i></li>
                        <li><i className="fal fa fa-bars"></i></li>
                    </ul>
                </div>
            </div>
        </header>
    )
}
